// src/components/LatestNews/LatestNews.js
import React from 'react';
import './LatestNews.css';
import article1Img from '../../assets/images/article1.jpg'; // Remplacez par le chemin rel de l'image
import article2Img from '../../assets/images/article2.jpg'; // Remplacez par le chemin rel de l'image
import { Link } from 'react-router-dom';

const LatestNews = () => (
  <section className="latest-news py-5">
    <div className="container">
      <div className="text-center mb-5">
        <h2>Dernires Nouvelles</h2>
        <p>Restez constamment  jour pour ne rien rater des nos nouvelles offres et possibilits.</p>
        <Link to='/news' className="btn btn-dark">Lire plus</Link>
      </div>
      <div className="row">
        <div className="col-md-6 mb-4">
          <div className="card">
            <div className="row no-gutters">
              <div className="col-md-4">
                <img src={article1Img} className="card-img" alt="Article 1" />
              </div>
              <div className="col-md-8">
                <div className="card-body">
                  <h5 className="card-title">Article 1</h5>
                  <p className="card-text">Profitez de notre promotion pour obtenir un site web simple ou statique en seulement 2 jours ! Obtenez un design personnalis, une navigation fluide et une optimisation SEO. Contactez-nous maintenant pour transformer vos ides en ralit numrique rapidement et  moindre cot.
                  </p>
                  <p className="card-text"><small className="text-muted">Julien Akim</small></p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6 mb-4">
          <div className="card">
            <div className="row no-gutters">
              <div className="col-md-4">
                <img src={article2Img} className="card-img" alt="Article 2" />
              </div>
              <div className="col-md-8">
                <div className="card-body">
                  <h5 className="card-title">Article 2</h5>
                  <p className="card-text">Nous dveloppons votre application web dynamique en seulement 14 jours grce  notre promotion exclusive ! Ces applications web peuvent assurer votre prsence en ligne ou grer votre petite entreprise directement depuis votre tlphone ou ordinateur, sans avoir besoin de vous dplacer. Ne manquez pas cette opportunit de booster votre efficacit et prsence en ligne rapidement et efficacement.</p>
                  <p className="card-text"><small className="text-muted">Julien Akim</small></p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default LatestNews;
